import axios, { AxiosError, type AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useAuthHeader } from 'react-auth-kit'
import { useMutation, useQuery, useQueryClient } from 'react-query'

import { type GridRowId } from '@mui/x-data-grid/models'

import { COMMON_MESSAGE } from 'src/data/messages'

import type { ClubId } from 'src/models/club'
import { type AmountCategory, type AmountType } from 'src/models/account'
import { type DateProps } from 'src/models/date'

export interface GetAccountInfoResponseData {
  totalIncome: number
  totalExpense: number
}
interface GetParameters {
  clubId: ClubId
  date: DateProps
  token: string
}
const getAccountInfoAPICall =
  ({ clubId, date, token }: GetParameters) =>
  async () => {
    const response = await axios.get<GetAccountInfoResponseData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/account?year=${date.year}&semester=${date.semester}`,
      {
        headers: { Authorization: token }
      }
    )
    return response.data
  }
export const useGetAccount = (clubId: ClubId, date: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetAccountInfoResponseData>(['account', date], getAccountInfoAPICall({ clubId, date, token }), {
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.SERVER_ERROR
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
      }
    }
  })
}

export interface GetAccountHistoryResponseData {
  id: number
  amountCategory: AmountCategory
  amountType: AmountType
  amount: number
  detail: string
  usedAt: Date
}
const getAccountHistoryAPICall =
  ({ clubId, date, token }: GetParameters) =>
  async () => {
    const response = await axios.get<GetAccountHistoryResponseData[]>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/account/history?semester=${date.semester}&year=${date.year}`,
      {
        headers: { Authorization: token }
      }
    )
    return response.data
  }
export const useGetAccountHistory = (clubId: ClubId, date: DateProps) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()

  return useQuery<GetAccountHistoryResponseData[]>(
    ['account_history', date],
    getAccountHistoryAPICall({ clubId, date, token }),
    {
      onError: error => {
        if (error instanceof AxiosError) {
          const errorMessage = error.response?.data?.message || COMMON_MESSAGE.SERVER_ERROR
          enqueueSnackbar(errorMessage, { variant: 'error' })
        } else {
          enqueueSnackbar(COMMON_MESSAGE.SERVER_ERROR, { variant: 'error' })
        }
      }
    }
  )
}

export interface PostAccountHistoryRequestData {
  amountCategory: AmountCategory
  amountType: AmountType | null
  amount: number
  detail: string
  usedAt: Date
}
interface PostParameters {
  clubId: ClubId
  date: DateProps
  data: PostAccountHistoryRequestData
  token: string
}
const postAccountHistoryAPICall =
  ({ clubId, date, data, token }: PostParameters) =>
  async () => {
    await axios.post<null, AxiosResponse<null>, PostAccountHistoryRequestData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/account/history?year=${date.year}&semester=${date.semester}`,
      data,
      {
        headers: { Authorization: token }
      }
    )
  }
export const usePostAccountHistory = (clubId: ClubId, date: DateProps, data: PostAccountHistoryRequestData) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(postAccountHistoryAPICall({ clubId, date, data, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.ADD_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('account')
      queryClient.invalidateQueries('account_history')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.ADD_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

export interface PutAccountHistoryRequestData {
  amountCategory: AmountCategory
  amountType: AmountType
  amount: number
  detail: string
  usedAt: Date
}
interface PutParameters {
  clubId: ClubId
  data: PostAccountHistoryRequestData
  editFeeId: number
  token: string
}
const putAccountHistoryAPICall =
  ({ clubId, data, editFeeId, token }: PutParameters) =>
  async () => {
    await axios.put<null, AxiosResponse<null>, PostAccountHistoryRequestData>(
      `${process.env.REACT_APP_APIHOST}/club/${clubId}/account/history/${editFeeId}`,
      data,
      {
        headers: { Authorization: token }
      }
    )
  }
export const usePutAccountHistory = (clubId: ClubId, editFeeId: number, data: PostAccountHistoryRequestData) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()

  return useMutation(putAccountHistoryAPICall({ clubId, data, editFeeId, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.UPDATE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('account')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.UPDATE_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}

interface DeleteParameters {
  clubId: ClubId
  deleteHistoryIds: number[]
  token: string
}
export interface DeleteAccountHistoriesRequestData {
  accountDetailIds: number[]
}
const deleteAccountHistoryAPICall =
  ({ clubId, deleteHistoryIds, token }: DeleteParameters) =>
  async () => {
    await axios({
      method: 'delete',
      url: `${process.env.REACT_APP_APIHOST}/club/${clubId}/account/history`,
      headers: { Authorization: token },
      data: { accountDetailIds: deleteHistoryIds }
    })
  }
export const useDeleteAccountHistory = (clubId: ClubId, selectedHistoryIds: GridRowId[]) => {
  const { enqueueSnackbar } = useSnackbar()
  const token = useAuthHeader()()
  const queryClient = useQueryClient()
  const deleteHistoryIds: number[] = selectedHistoryIds.map(id => Number(id))

  return useMutation(deleteAccountHistoryAPICall({ clubId, deleteHistoryIds, token }), {
    onSuccess: () => {
      enqueueSnackbar(COMMON_MESSAGE.DELETE_SUCCESS, { variant: 'success' })
      queryClient.invalidateQueries('feeAmounts')
    },
    onError: error => {
      if (error instanceof AxiosError) {
        const errorMessage = error.response?.data?.message || COMMON_MESSAGE.DELETE_FAIL
        enqueueSnackbar(errorMessage, { variant: 'error' })
      } else {
        enqueueSnackbar(COMMON_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
      }
    }
  })
}
