import { useSnackbar } from 'notistack'
import type { FC } from 'react'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router'

import { MobileDatePicker } from '@mui/lab'
import { Box, Card, CardHeader, Checkbox, FormControlLabel, Grid, MenuItem, TextField } from '@mui/material'

import amountCategoryList from 'src/data/amountCategoryList'
import { ACCOUNT_MESSAGE } from 'src/data/messages'

import type { AmountCategory, Fee } from 'src/models/account'
import { type DateProps } from 'src/models/date'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import { EXPENSE_COLOR_BACKGROUND, INCOME_COLOR_BACKGROUND } from 'src/content/manage/Account//accountPageColors'
import AmountTypeToggle from 'src/content/manage/Account/AmountTypeToggle'

import {
  type PostAccountHistoryRequestData,
  usePostAccountHistory,
  usePutAccountHistory
} from 'src/hooks/api/accountHistory'

interface HistoryInputProps {
  clickedFee?: Fee
  refresh: () => void
  date: DateProps
}
const HistoryInput: FC<HistoryInputProps> = ({ clickedFee = null, refresh, date }) => {
  const defaultState: PostAccountHistoryRequestData = {
    amountType: null,
    amountCategory: '회비',
    amount: 0,
    usedAt: new Date(),
    detail: ''
  }
  const [accountHistoryDetail, setAccountHistoryDetail] = useState<PostAccountHistoryRequestData>(defaultState)
  const [isEmptyAfterSubmit, setIsEmptyAfterSubmit] = useState(true)
  const [isEditMode, setIsEditMode] = useState(clickedFee !== undefined)

  const params = useParams()
  const clubId = Number(params.id)

  const { enqueueSnackbar } = useSnackbar()

  const putAccountHistoryMutation = usePutAccountHistory(clubId, clickedFee?.id ?? -1, accountHistoryDetail)
  const postAccountHistoryMutation = usePostAccountHistory(clubId, date, accountHistoryDetail)

  const { amountType, amountCategory, amount, usedAt, detail } = accountHistoryDetail

  useEffect(() => {
    const isClickedFeeExists = !!clickedFee
    isClickedFeeExists &&
      setAccountHistoryDetail({
        amountType: clickedFee?.amountType,
        amountCategory: clickedFee?.amountCategory,
        amount: clickedFee?.amount,
        usedAt: clickedFee?.usedAt,
        detail: clickedFee?.detail
      })
    setIsEditMode(isClickedFeeExists)
  }, [clickedFee])

  const handleHistoryDetailChange: (
    label: string
  ) => React.ChangeEventHandler<HTMLInputElement | HTMLTextAreaElement> = label => e => {
    // 건당 10억이상 넘지 못하게 처리
    if (label === 'amount' && Number(e.target.value) > 1000000000) return

    const value = label === 'amount' ? Number(e.target.value) : e.target.value

    setAccountHistoryDetail({ ...accountHistoryDetail, [label]: value })
  }

  const handleSubmit: React.MouseEventHandler<HTMLButtonElement> = async () => {
    if (detail.length === 0) {
      enqueueSnackbar(ACCOUNT_MESSAGE.BLANK_DETAIL, { variant: 'error' })
      return
    } else if (amount < 1) {
      enqueueSnackbar(ACCOUNT_MESSAGE.INVALID_AMOUNT, { variant: 'error' })
      return
    }

    const putDataId = clickedFee?.id

    try {
      isEditMode && putDataId
        ? await putAccountHistoryMutation.mutateAsync()
        : await postAccountHistoryMutation.mutateAsync()

      if (isEmptyAfterSubmit) {
        handleEmptyValues()
      }

      refresh()
    } catch (err) {}
  }

  const handleEmptyValues = () => {
    setAccountHistoryDetail({ ...defaultState, amountType: accountHistoryDetail.amountType })
    setIsEditMode(false)
  }

  const handleClickEmptyAfterSubmit = () => {
    setIsEmptyAfterSubmit(!isEmptyAfterSubmit)
  }

  return (
    <Card style={{ marginBottom: 10 }}>
      <CardHeader
        title={
          <Box display={'flex'} flexDirection={'row'} alignItems={'center'} justifyContent={'space-between'}>
            {amountType && (amountType === 'INCOME' ? '입금' : '출금')}
            내역&nbsp;
            {isEditMode ? '수정' : '추가'}
            <AmountTypeToggle
              filteredFeeType={amountType}
              setFilteredFeeType={(event, value) => {
                setAccountHistoryDetail({ ...accountHistoryDetail, amountType: value })
              }}
            />
          </Box>
        }
        style={{
          backgroundColor: amountType
            ? amountType === 'INCOME'
              ? INCOME_COLOR_BACKGROUND
              : EXPENSE_COLOR_BACKGROUND
            : 'transparent'
        }}
      />
      {amountType !== null && amountType !== undefined && (
        <Box p={2}>
          <Grid container justifyContent={'center'} spacing={2}>
            <Grid item sm={true} md={3}>
              <TextField
                label={'분류'}
                id={'amountCategory'}
                select
                fullWidth
                value={amountCategory}
                onChange={handleHistoryDetailChange('amountCategory')}
              >
                {amountCategoryList &&
                  amountCategoryList.map((category: AmountCategory, index) => {
                    return (
                      <MenuItem key={index} value={category}>
                        {category}
                      </MenuItem>
                    )
                  })}
              </TextField>
            </Grid>
            <Grid item sm={true} md={3}>
              <TextField
                label={'내용(30자 이내)'}
                id={'detail'}
                value={detail}
                fullWidth
                inputProps={{ maxLength: 30 }}
                onChange={handleHistoryDetailChange('detail')}
              ></TextField>
            </Grid>
            <Grid item sm={true} md={3}>
              <TextField
                label={'금액'}
                id={'amount'}
                value={amount}
                fullWidth
                type={'number'}
                onChange={handleHistoryDetailChange('amount')}
              ></TextField>
            </Grid>
            <Grid item sm={true} md={3} data-testid="datePicker">
              <MobileDatePicker
                label={'일자'}
                inputFormat={'yyyy/MM/dd'}
                value={usedAt}
                onChange={e => {
                  if (e !== null) {
                    setAccountHistoryDetail({ ...accountHistoryDetail, usedAt: e })
                  }
                }}
                renderInput={params => <TextField {...params} fullWidth />}
              />
            </Grid>
          </Grid>
          <Box mt={2} sx={{ display: 'flex', justifyContent: 'center' }}>
            <FormControlLabel
              label={'등록 후 값 비우기'}
              control={<Checkbox checked={isEmptyAfterSubmit} onChange={handleClickEmptyAfterSubmit} />}
            />
          </Box>

          <CancelSubmitBtns
            handleSubmit={handleSubmit}
            handleCancel={handleEmptyValues}
            submitBtnText={isEditMode ? '수정하기' : '추가하기'}
            cancelBtnText={'값 비우기'}
          />
        </Box>
      )}
    </Card>
  )
}

export default HistoryInput
